<template>
  <section class="login-card elevation-10 d-flex mx-auto rounded-xxl">
    <div class="login-container-right d-flex flex-column mx-auto">
      <app-breadcrumb-button class="py-5 pr-4" :breadcrumbs="breadcrumbs"></app-breadcrumb-button>
      <!-- Reset Password Form -->
      <v-form ref="resetPasswordForm" class="login-form ma-auto text-start" @submit.prevent="submitResetPassword">
        <v-row no-gutters>
          <v-col cols="12">
            <v-row no-gutters class="pt-6 px-0" align="center" justify="space-around">
              <v-col cols="12">
                <p class="text-caption secondary--text font-weight-light">{{ $t(`label.resetPassword`) }}</p>
              </v-col>
              <v-col sm="12" cols="12" class="pb-0">
                <v-row no-gutters>
                  <v-col cols="12" class="pb-4">
                    <div class="pr-15">
                      <v-text-field
                        class="v-input input-field text-caption v-input--hide-details v-input--dense theme--light v-text-field v-text-field--full-width v-text-field--single-line v-text-field--is-booted v-text-field--enclosed v-text-field--outlined v-text-field--placeholder"
                        v-model.trim="resetPasswordObj.password"
                        :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="showPassword ? 'text' : 'password'"
                        @click:append="showPassword = !showPassword"
                        placeholder="Password"
                      ></v-text-field>
                    </div>
                  </v-col>

                  <v-col cols="12" class="pb-4">
                    <div class="pr-15">
                      <v-text-field
                        class="v-input input-field text-caption v-input--hide-details v-input--dense theme--light v-text-field v-text-field--full-width v-text-field--single-line v-text-field--is-booted v-text-field--enclosed v-text-field--outlined v-text-field--placeholder"
                        v-model.trim="resetPasswordObj.passwordConfirm"
                        :append-icon="showPasswordConfirm ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="showPasswordConfirm ? 'text' : 'password'"
                        @click:append="showPasswordConfirm = !showPasswordConfirm"
                        placeholder="confirm Password"
                      ></v-text-field>
                    </div>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-col>
          <v-col cols="12" class="py-0">
            <v-row no-gutters align="center" justify="center">
              <v-col cols="12" class="py-0 text-center pb-sm-6">
                <app-button class="primary mt-5 w-100 font-weight-bold" type="submit" :text="$t('label.resetPassword')"></app-button>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-form>
    </div>
  </section>
</template>

<script>
import { USER_RESET_PASSWORD } from '@/store/user.module'
import { localizationHelper } from '@/utils'
import { ROUTE_NAME } from '@/constants'

export default {
  name: 'ResetPasswordView',
  components: {},
  props: {},
  data() {
    return {
      showPassword: false,
      showPasswordConfirm: false,
      resetPasswordObj: {
        password: '',
        passwordConfirm: '',
        token: ''
      },
      breadcrumbs: [
        {
          text: localizationHelper.getMessage('button.back'),
          disabled: false,
          to: {
            name: ROUTE_NAME.LOGIN
          },
          exact: true
        }
      ]
    }
  },
  created() {},
  mounted() {},
  computed: {},
  watch: {},
  methods: {
    submitResetPassword() {
      if (this.$refs.resetPasswordForm.validate()) {
        const resetPasswordObj = this.resetPasswordObj
        // resetPasswordObj.token = this.$route.path.substring(1).split('/')[1]
        resetPasswordObj.token = this.$route.query.token
        this.$store.dispatch(`${USER_RESET_PASSWORD}`, { resetPasswordObj })
      }
    }
  },
  beforeDestroy() {}
}
</script>

<style lang="scss" scoped>
.rounded-xxl {
  border-radius: 10px;
}

.login-card {
  width: max(60vw, 350px);
  max-width: 1200px;
  background-color: white;
}

.login-form {
  max-width: max(50%, 300px);
}

.login-container-right {
  min-height: max(450px, 36vh);
}
</style>
